import Squire from './components/Squire'
import Input from './components/Input'
import { useState } from 'react'
function App() {
  const [colorValue, serColorValue] = useState('')
  const [hexValue, serHexValue] = useState('')
  const [isDark, setDark] = useState(true)
  return (
    <div className="App">
      <Squire
        colorValue={colorValue}
        hexValue={hexValue}
        isDark={isDark}
      ></Squire>
      <Input
        colorValue={colorValue}
        serColorValue={serColorValue}
        serHexValue={serHexValue}
        isDark={isDark}
        setDark={setDark}
      ></Input>
    </div>
  );
}

export default App;
