import React from 'react'
import colorNames from 'colornames'

function Input({ colorValue, serColorValue, serHexValue, setDark, isDark }) {
    return (
        <form onSubmit={e => e.preventDefault()}>
            <label>Add Color Name : </label>
            <input
                autoFocus
                type="text"
                placeholder="Add Color Name"
                required
                value={colorValue}
                onChange={(e) => {
                    serColorValue(e.target.value)
                    serHexValue(colorNames(e.target.value))
                }}
            />
            <button
                type="button"
                onClick={() => setDark(!isDark)}
            >Change bgColor</button>
        </form>
    )
}

export default Input
